import React,{useState, useEffect} from 'react';
import queryString from "query-string" //get data from query in URL
import io from "socket.io-client"

import "./Chat.css"
import InfoBar from '../InfoBar/InfoBar';
import Input from '../Input/Input';
import Messages from '../Messages/Messages';
import TextContainer from '../TextContainer/TextContainer';

let socket;

const Chat = ({location}) => {
  const [name, setName] = useState("")
  const [room, setRoom] = useState("")
  const [message, setMessage] = useState("")
  const [messages, setMessages] = useState([])
  const [users, setUsers] = useState([])

  const ENDPOINT = 'https://chat-app-socketio-test.herokuapp.com/'
  useEffect(() => {
    const {name, room} = queryString.parse(location.search) //location from router
    console.log(location.search)
    socket = io(ENDPOINT)

    setName(name)
    setRoom(room)
    console.log(name, room)
    console.log(socket)
    
    socket.emit('join', {name:name, room:room}, ()=>{}) //emit 1 event de server catch cai event do 
    
    return () => {  //unmounting the component
      socket.emit("disconnect")
      socket.off()
    }

  }, [ENDPOINT, location.search]);

  useEffect(() => {// message l√† payload c√≥ {user, text}
    socket.on("message", (message) => {
      setMessages([...messages, message])
    })

    socket.on("roomData", (roomInfo) => {
      setUsers(roomInfo.users)
    })
  }, []);

  //function for sending messages
  const sendMessage = (e) =>{
    e.preventDefault()
    if(message){
      socket.emit("sendMessage", message, () => setMessage("")) // send xong th√¨ l·∫°i r·ªóng 
    }
  }

  console.log(message, messages)

  return (
    <div className="outerContainer">
      <div className="container">
        {/* <input 
        value={message} 
        onChange={(e) => setMessage(e.target.value)}
        onKeyPress={(e) => e.key === "Enter" ? sendMessage(e): null}
        /> */}
        <InfoBar room={room}/>
        <Messages messages={messages} name={name} />
        <Input message={message} setMessage={setMessage} sendMessage={sendMessage}/>
      </div>
      <TextContainer users={users}/>
    </div>
  );
}

export default Chat;
